.container {
  height: 100%;
  // overflow: auto;
  // display: flex;
  // align-items: center;
  // position: relative;
  user-select: none;
  display: flex;
  flex-direction: column;

  .header {
    height: 60px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    padding: 0 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .scale {
    justify-content: center;
  }

  .content {
    flex: 1;
    overflow: auto;
    display: flex;
    // align-items: center;
    // justify-content: center;
    align-items: safe center;
    justify-content: safe center;
    padding: 16px;
  }

  .panel {
    position: relative;
    box-shadow: 0px 0px 8px #333333;
    max-width: 100%;
    font-size: 0;

    .contours,
    .centers {
      display: block;
      position: absolute;
      top: 0;
      left: 0;

      &.hidden {
        display: none;
      }
    }

    &.hidden {
      display: none;
    }
  }
}
