.layout {
  display: flex;
  width: 100%;
  &.reverse {
    flex-direction: row-reverse;
    .miniContainer {
      border: 0;
      border-right: 1px solid rgba(0, 0, 0, 0.08);
    }
  }
  .miniContainer {
    width: 320px;
    flex-shrink: 0;
    height: 100%;
    border: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.08);
    background-color: rgb(248, 248, 248);
    display: flex;
    flex-direction: column;
    .iconWrap {
      flex-shrink: 0;
    }
    .menuWrap {
      height: 100%;
    }
  }

  .mainContainer {
    width: 100%;
    height: 100%;
    flex: 1;
    overflow: auto;
  }
}
